<template >
    <transition name="mod_tran" >
        <div class="mod" :style='styleObj'  v-if="current == myOrder">
            <!-- 箭头 -->
            <p class="triangle" :style='direction'></p>
            <p class="highRegion" :style='highRegionObj'></p>
            <!-- 当前和总数量显示 -->
            <p>{{order}} / {{mycurrent}}</p>
            <!-- 内容 -->
            <p>{{modContent}}</p> 
            <!-- 按键 -->
            <div class="modBtnBox">
                <!-- 执行到第一个引导框就取消上一步 -->
                <p class="modBtn" @click="offMod('prev')" v-if="mycurrent !== current">上一步</p>
                <!-- 执行最后引导框就显示完成 -->
                <p class="modBtn highlight" @click="offMod('next')">{{current == 1?'完成':'下一步'}}</p>          
            </div>
            
        </div>
    </transition>
</template>
<script>
export default {
    props:{
        styleObj: {   //提示框样式
            type:Object, 
            default:() =>{
                return {}
            }
        }, 
        highRegionObj:{   //高亮区域参数
            type:Object, 
            default:() =>{
                return {}
            }
        },
        modDirection:{   //提示框样式
            type:String, 
            default:''
        }, 
        modContent:{    //提示文字
            type:String,
            default:'未设置提示文字'
        },
        order:{    //当前组件的执行唯一数 5
            type:Number,
        },
        current:{   //当前执行的顺序  5
            type:Number,
        }

    },
    data(){
        return{
            mycurrent :this.current ,   //保存引导框的总数量
            direction :{} ,             //箭头方向
            directionMap:{              //方向对应值
                left : {top:'20px' , left:'-10px'},
                bottom : {bottom:'-10px' , left:'45%'},
                right :{top:'20px' , right:'-10px'},
                top : {top:'-10px' , left:'45%'},
                topRight : {top:'-10px' , left:'80%'}
            }
            
        }
    },
    watch:{
        // 监听箭头方向变化
        modDirection:{
            handler (newVal){
                this.direction = this.directionMap[newVal]
            },
            immediate:true
        }
    },
    computed:{
        // 转化order值，这样外部传入mod执行1234顺序
        myOrder(){
            return this.mycurrent-this.order+1
        }
    },
    methods: {
        // 上一步下一步事件
        offMod(type){
            this.$emit('offMod',type)
        }
        
    },
}
</script>
<style lang="less" scoped>
.mod_tran-enter-active, .mod_tran-leave-active {
transition: opacity .5s ease;
}
.mod_tran-enter, .mod_tran-leave-to  {
opacity:  0;
}
.mod{
    min-width:80px ;
    width: 100px;
    padding: 10px;
    position: absolute;
    border-radius: 5px;
    top: 0;
    left: 100%;
    background: #fff;
    z-index: 120;
    color: rgb(0, 0, 0);
    line-height: 30px;
    .triangle{
        position: absolute;
        width: 20px;
        height: 20px;
        background: #fff;
        transform: rotate(45deg);
    }
    .highRegion{
        position: absolute;
        // background: rgba(255, 255, 255, 0.);
        border-radius: 3px;
        
        border: 2px solid rgb(255, 255, 255);
        box-shadow: rgb(255, 255, 255) 2px 2px 10px inset;
        border-radius: 5px;
        box-sizing: border-box;
    }
    .modBtnBox{
        float: right;
        .modBtn{
            border-radius: 5px;
            background: rgb(130, 184, 255);
            width: 80px;
            cursor: pointer;
            color: #fff;
            font-size: 14px;
            float: left;
            margin: 10px;
            &.highlight{
                background: rgb(0, 110, 255);
            }
        }
    }
    
}
    
</style>
   
