<template>
  <div class="home">
    <!-- 页面引导时显示的遮罩层 -->
    <div class="pageMask" v-if="current"></div>
    <!-- 页头部分 -->
    <div class="header">
      <!-- header引导框 -->
      <Mod
        :current="current"
        :order="4"
        :styleObj="{ width: '200px', top: '73px', left: '45%' }"
        :highRegionObj="{
          right: '43%',
          top: '-63px',
          width: '30px',
          height: '30px',
          borderColor: 'blue',
          borderRadius: '50%',
        }"
        :modDirection="'top'"
        :modContent="'页面头部信息展示位置'"
        @offMod="offMod"
      ></Mod>
      <p class="loginMsg">
        注册登录
        <!-- 右上角引导框 -->
        <Mod
          :current="current"
          :order="5"
          :styleObj="{ width: '200px', top: '75px', left: '-257%' }"
          :highRegionObj="{
            right: '-10%',
            top: '-71px',
            width: '90px',
            height: '40px',
            borderColor: 'blue',
          }"
          :modDirection="'topRight'"
          :modContent="'个人信息，登录、退出、设置等'"
          @offMod="offMod"
        ></Mod>
      </p>
    </div>
    <!-- 底部内容部分，包括导航和内容 -->
    <div
      class="contentBox"
      :class="{ min: screenWidth < 700, med: screenWidth < 1200 }"
    >
      <!-- 导航盒子 -->
      <div class="navBox">
        <!-- 导航盒子布局 -->
        <div class="navBox_border" :class="{ clearPadding: !switchNavBox }">
          <!-- 子导航引导框 -->
          <Mod
            :current="current"
            :order="1"
            :styleObj="{ width: '200px', top: '3px', left: '120%' }"
            :highRegionObj="{
              right: '117%',
              top: '17px',
              width: '30px',
              height: '30px',
              borderRadius: '50%',
            }"
            :modDirection="'left'"
            :modContent="'点击带箭头的导航可以展开子级导航菜单！！！！'"
            @offMod="offMod"
          ></Mod>
          <!-- 展开收起导航引导框 -->
          <Mod
            :current="current"
            :order="2"
            :styleObj="{ width: '200px', top: '150px', left: '120%' }"
            :highRegionObj="{
              right: '118%',
              top: '14px',
              width: '100px',
              height: '30px',
            }"
            :modDirection="'left'"
            :modContent="'点击收起按键，隐藏整体导航'"
            @offMod="offMod"
          ></Mod>
          <!-- 导航组件 -->
          <my-transition>
            <MyNav
              v-show="switchNavBox"
              :actNav="actNav"
              :navList="navList"
              @changeNavAct="changeNavAct"
            ></MyNav>
          </my-transition>
          <!-- 切换整个导航显示隐藏按键 -->
          <div class="navBut" @click="switchNavBox = !switchNavBox">
            {{ switchNavBox ? "收起导航" : "展开导航" }}
          </div>
        </div>
      </div>
      <!-- 内容部分 -->
      <div class="content">
        <!-- 内容引导框 -->
        <Mod
          :current="current"
          :order="3"
          :styleObj="{ width: '30%', top: '100px', left: '35%' }"
          :highRegionObj="{ width: '0', height: '0' }"
          :modDirection="'top'"
          :modContent="'点击上传文件或者文件夹'"
          @offMod="offMod"
        ></Mod>
        <router-view />
      </div>
    </div>
  </div>
</template>
<script>
// 自定义过度组件
import myTransition from "../utils/donghua.js";
// 导航组件
import MyNav from "../components/myNav.vue";
// 引导框组件
import Mod from "../components/mod.vue";
export default {
  name: "Home",
  components: {
    MyNav,
    Mod,
    "my-transition": myTransition,
  },
  data() {
    return {
      current: 5, //引导框初始显示
      screenWidth: "", //页面宽度
      switchNavBox: true, //收起展开控制
      actNav: "upload", //当前选中的导航
      navList: [
        //多层级导航数据
        {
          name: "A1",
          children: [
            {
              name: "B1",
              children: [{ name: "C1" }, { name: "C2" }],
            },
            { name: "B2" },
            { name: "B3" },
          ],
        },
        {
          name: "A2",
          children: [
            { name: "B4" },
            { name: "B5" },
            { name: "B6" },
            { name: "B7" },
            { name: "B8" },
            { name: "B9" },
          ],
        },
        {
          name: "upload",
        },
      ],
    };
  },
  created() {
    // 初始化引导功能是否显示
    if (localStorage.getItem("Mask") == "false") {
      this.current = null;
    }
  },
  mounted() {
    this.monitorWidth(); //监听页面宽度
  },

  methods: {
    // 引导框关闭
    offMod(type) {
      type == "next" ? this.current-- : this.current++;
      if (this.current == 0) {
        localStorage.setItem("Mask", "false"); //保存引导状态，下次进入不在提示引导页面
      }
    },
    // 监听页面宽度
    monitorWidth() {
      //初始化页面宽度
      this.screenWidth = document.body.clientWidth;
      window.addEventListener("resize", () => {
        //页面宽度变化继续监听
        this.screenWidth = document.body.clientWidth;
      });
    },
    // 导航变化
    changeNavAct(name) {
      this.actNav = name; //当前选中页面
      this.$router.push({ path: name }); //跳转对应页面
    },
  },
};
</script>
