<template>
  <ul class="nav">
    <li
      class="navItem"
      v-for="item in navList"
      :key="item.name"
      @click.stop="openNav(item.name, item.children)"
    >
      <!-- 显示导航每一项  name和icon -->
      <p :class="{ act: actNav == item.name }">
        <span :class="{ boldNav: item.children }">{{ item.name }}</span>
        <span
          v-if="item.children"
          class="switchIcon iconfont"
          :class="isShow(item.name) ? 'icon-jiantouxia' : 'icon-jiantoushang'"
        ></span>
      </p>
      <!-- 递归有子导航的 -->
      <my-transition>
        <myNav
          :actNav="actNav"
          v-show="isShow(item.name)"
          :navList="item.children"
          @changeNavAct="changeNavAct"
        ></myNav>
      </my-transition>
    </li>
  </ul>
</template>

<script>
// 引入过度函数组件
import myTransition from "../utils/donghua.js";
export default {
  name: "myNav",
  props: ["navList", "actNav"],
  components: {
    "my-transition": myTransition,
  },
  data() {
    return {
      clickNavList: [], //保存要展开子导航的父
    };
  },
  computed: {
    /*
    判断传入导航name是否存在点击导航数组中
    存在就返回true ,显示
    不存在就关闭对应子导航
    */ 
    isShow() {
      return function (name) {
        return this.clickNavList.includes(name);
      };
    },
  },
  methods: {
    // 通知父元素改变选中的导航
    changeNavAct(name, child) {
      this.openNav(name, child);
    },
    // 点击一级导，展开子级导航
    openNav(name, child) {
      if (!child) {
        this.$emit("changeNavAct", name);
      }
      let navIndex = this.clickNavList.indexOf(name);    // 判断点击导航数组中是否包含，当前点击导航name
      if (navIndex !== -1) {
        this.clickNavList.splice(navIndex, 1);           // 包含就从数组中删除，关闭子导航
      } else {
        this.clickNavList.push(name);                    //不包含就追加到数据，展开子导航
      }
    },
  },
};
</script>
<style scoped lang="less">
.nav {
  text-align: left;
  color: rgb(129, 129, 129);
  .navItem {
    p {
      margin: 0;
      background: #fff;
      line-height: 40px;
      cursor: pointer;
      padding: 5px 20px;
      transition: 0.5s;
      &.act {
        color: rgb(0, 140, 255);
        background: rgb(213, 235, 255);
      }
      &:hover {
        color: rgb(0, 140, 255);
        background: rgb(213, 235, 255);
      }
      .boldNav {
        font-weight: 700;
        color: #000;
      }
      .switchIcon {
        float: right;
        font-size: 14px;
      }
    }
  }
}
</style>
